@use 'sass:map';
@use '../abstracts/variables';
@use '../abstracts/functions';
@use '../abstracts/mixins';

/* 회원 공통 */
// 인증 타입 선택 버튼
.cert_type_box {
	@include mixins.justify_width();
	align-items: stretch;

	.btn_cert_type {
		@include mixins.justify_width(center, center, column, inline);
		flex: 1;
		height: 124px;
		padding: 0 14px;
		background: #fff;
		border: 1px solid variables.$colorGray03;
		border-radius: 10px;
		text-align: center;
		box-sizing: border-box;

		.text {
			display: block;
			margin-top: 12px;
			color: variables.$textColorBlack;
			@include mixins.font_size_sm;
		}

		[class^=ico_]{
			display: inline-block;
			height: 35px;
			margin-top: 2px;
			vertical-align: top;
		}
		.ico_phone {
			width: 35px;
			background: {
				position: center;
				repeat: no-repeat;
			}
			@include mixins.ratio_images('../images/ico_cert_phone_mok', 'png', 35px, 35px);
		}
		.ico_ipin {
			width: 37px;
			background: {
				position: center;
				repeat: no-repeat;
			}
			@include mixins.ratio_images('../images/ico_cert_ipin_mok', 'png', 37px, 31px);
		}

		& + .btn_cert_type {
			margin-left: 5px;
		}
	}

	& + .info_text_box {
		margin-top: 10px;
	}

	& + .info_text {
		margin-top: 10px;
	}
}

// 상단 인증 결과 아이콘 영역
.ico_info_wrap {
	text-align: center;
	box-sizing: border-box;

	.ico_img_box {
		margin-bottom: 20px;

		[class^=ico_] {
			display: inline-block;
			vertical-align: top;
		}
	}

	.ico_text_box {
		.ico_title {
			display: block;
			color: variables.$textColorBlack;
			@include mixins.font_size_xl;
			font-weight: variables.$fontWeightBold;

			& ~ .ico_desc {
				margin-top: 6px;
			}
		}
		.ico_desc {
			display: block;
			color: variables.$textColorGray;
			@include mixins.font_size_sm;
			font-weight: variables.$fontWeightRegular;
		}
	}
}

.wrapper {
	&.member_login{
		@include mixins.justify_width(start, start, column);
		align-items: stretch;
		min-height: 100%;
		box-sizing: border-box;

		.header_wrapper {
			.header_center {
				margin-right: 30px;
			}
		}

		.container_wrapper {
			width: 100%;
			padding-top: 60px;
			padding-bottom: 8px;

			.contents_wrap{
				margin: 0 auto;

				.contents_inner {
					padding-top: 20px;
				}

				// 상단 로고
				.member_logo_box {
					padding-bottom: 50px;
					text-align: center;

					.logo_link {
						display: inline-block;
						width: 163px;
						height: 47px;

						img {
							width: 100%;
						}
					}
				}

				// 상단 인증 결과 아이콘 영역
				.ico_info_wrap {
					padding-bottom: 50px;
					margin-bottom: 30px;
					border-bottom: 1px solid variables.$colorGray04;

					&.no_line {
						border-bottom: none;
						padding-bottom: 0;
					}
				}

				// 비밀번호 유효성 검사(공통으로 사용되는 경우 scss 이동)
				.pw_valid_wrap{
					margin-top: 6px;

					.valid_step_box{
						@include mixins.justify_width(center);

						.valid_step_list{
							width: 55%;
							max-width: 186px;
							margin-right: 15px;
							font-size: 0;

							.step_item{
								display: inline-block;
								width: calc((100% - 3px * 2) / 3);
								height: 3px;
								background-color: variables.$colorGray04;
								border-radius: 2px;
								vertical-align: top;

								&:before{
									content: '';
									position: absolute;
									width: 10px;
									height: 7px;
									left: 0;
									top: 3px;
									@include mixins.ratio_images('../images/arw_valid_step_mok', 'png', 10px, 7px);
								}

								+ .step_item{
									margin-left: 3px;
								}
							}
						}

						.bubble_speech_box{
							margin-left: auto;

							.valid_state{
								display: block;
								@include mixins.font_size_xs;
							}
						}

						&.valid_fail{
							.valid_state{
								color: variables.$textColorBlack;
							}
						}

						&.valid_warning{
							.valid_step_list{
								.step_item{
									&:first-child {
										background-color: variables.$textColorRed;
									}
								}
							}

							.valid_state{
								color: variables.$textColorRed;
							}
						}

						&.valid_normal{
							.valid_step_list{
								.step_item{
									&:nth-child(-n + 2) {
										background-color: #ffbf00;
									}
								}
							}

							.valid_state{
								color: variables.$textColorBlack;
							}
						}

						&.valid_success{
							.valid_step_list{
								.step_item{
									background-color: variables.$kyoboGreen;
								}
							}

							.valid_state{
								color: variables.$kyoboGreen;
							}
						}
					}

					.pw_valid_list{
						margin-top: 4px;

						.pw_valid_item{
							padding-left: 14px;
							@include mixins.font_size_xs;
							@include mixins.ratio_images('../images/arw_valid_step_mok', 'png', 10px, 7px);
							background: {
								position: left 6px;
								repeat: no-repeat;
							}
							color: variables.$textColorLightGray;

							+ .pw_valid_item{
								margin-top: 6px;
							}

							&.valid_success{
								@include mixins.ratio_images('../images/arw_valid_step_success_mok', 'png', 10px, 7px);
								color: variables.$textColorGreen;
							}

							&.valid_fail{
								@include mixins.ratio_images('../images/arw_valid_step_fail_mok', 'png', 10px, 7px);
								color: variables.$textColorRed;
							}
						}
					}
				}

				// login
				.login_form_wrap {

					.title_wrap{
						text-align: center;

						.title_heading{
							width: 100%;
							
							.next_ico {
								&:after {
									content: '';
									display: inline-block;
									margin-left: 6px;
									background-repeat: no-repeat;
									background-position: center;
								}
								&.kakao {
									&.kakao{
										&:after {
											width: 24px;
											@include mixins.ratio_images('../images/ico_sns_kakao_mok', 'png', 24px, 24px);
										}
									}
								}
							}
						}
						&.title_size_xl {
							.title_heading {
								.next_ico {
									&:after {
										height: 24px;
										vertical-align: -5px;
									}
								}
							}
						}
					}

					.form_col_group{
						&.valid_check{
							&.fail{
								.col_box{
									input{
										border-color: variables.$textColorRed;
									}
								}
							}

							.col_box{
								&.fail {
									input {
										border-color: variables.$textColorRed;
									}

									~ .valid_desc{
										display: block;
									}
								}

								&.id{
									.form_desc{
										display: none;
									}

									&.focus{
										.form_desc{
											display: block;
										}

										& + .col_box {
											margin-top: 20px;
										}
									}
								}
							}

							.valid_desc{
								padding-left: 16px;
								color: variables.$textColorRed;

								&:before {
									content: '';
									display: inline-block;
									position: absolute;
									left: 0;
									top: 3px;
									width: 12px;
									height: 12px;
									@include mixins.ratio_images('../images/ico_warning_mok', 'png', 12px, 12px);
								}
							}
						}
					}

					.btn_wrap {
						margin-top: 20px;

						.btn_more_view {
							& ~ .btn_more_view {
								margin-left: 24px;
							}
						}
					}

					.save_id_box {
						margin-top: 12px;
						@include mixins.justify_width(center);

						.btn_text_link {
							margin-left: auto;
							color: variables.$textColorLightGray;
						}
					}

					.sns_login_box {
						margin-top: 40px;
						text-align: center;

						.sns_login_list {
							font-size: 0;

							.sns_login_item {
								display: inline-block;
								width: 42px;
								height: 42px;
								vertical-align: top;

								.btn_sns_login{
									display: block;
									height: 100%;

									&.kakao{
										@include mixins.ratio_images('../../images/ico_sns_kakao_mok', 'png', 42px, 42px);
									}

									&.naver{
										@include mixins.ratio_images('../../images/ico_sns_naver_mok', 'png', 42px, 42px);
									}

									&.google{
										@include mixins.ratio_images('../../images/ico_sns_google_mok', 'png', 42px, 42px);
									}

									&.fb{
										@include mixins.ratio_images('../../images/ico_sns_fb_mok', 'png', 42px, 42px);
									}

									&.apple{
										@include mixins.ratio_images('../../images/ico_sns_apple_mok', 'png', 42px, 42px);
									}
								}

								~ .sns_login_item{
									margin-left: 14px;

									@include mixins.respond-to(mobileMin){
										margin-left: 7px;
									}
								}
							}
						}
					}

					.form_box, .form_col_group {
						& + .chk_row_list {
							margin-top: 20px;
							margin-bottom: 8px;
						}
					}
				}

				.join_induce_wrap {
					margin-top: 40px;

					&.has_bd_top {
						padding-top: 40px;
						border-top: 1px solid variables.$colorGray04;
					}

					.align_center {
						text-align: center;
					}
					
					.form_box {
						& + .info_text {
							margin-top: 16px;
							text-align: center;
						}
					}

					.join_induce_inner {
						@include mixins.justify_width(center, start);
						padding-bottom: 24px;
						border-bottom: 1px solid variables.$colorGray04;

						.info_text {
							flex: 1;
						}
						.btn_wrap {
							flex: 0 auto;
							margin-left: 28px;

							.btn_sm {
								min-width: 165px;
							}
						}

						@include mixins.respond-to(mobileXs){
							flex-direction: column;
							text-align: center;

							.btn_wrap {
								margin-left: 0;
								margin-top: 10px;
							}
						}

						& + .btn_wrap {
							margin-top: 20px;
						}
					}

					.title_wrap {
						justify-content: center;
					}
				}

				.login_banner_box {
					margin-top: 40px;

					.banner_link {
						img {
							display: block;
							width: 100%;
						}
					}
				}

				.captcha_wrap {
					margin-top: 20px;

					.captcha_inner {
						position: relative;
						padding-right: 40px;

						.captcha_img_box {
							width: 100%;
							height: 72px;
							border: 1px solid variables.$colorGray03;
							overflow: hidden;
							border-radius: 6px;
							background-color: #F9F9FB;
							box-sizing: border-box;
							text-align: center;

							img {
								width: 100%;
								height: 100%;
								object-fit: contain;
							}
						}

						.btn_wrap {
							flex-direction: column;
							position: absolute;
							right: 0;
							top: 0;
							width: 34px;
							height: 100%;
							margin: 0;

							.btn_captcha {
								width: 34px;
								height: 34px;
								background: #fff;
								border: 1px solid variables.$colorGray03;
								border-radius: 6px;
								line-height: 32px;

								.ico_refresh {
									display: inline-block;
									width: 13px;
									height: 13px;
									margin-top: 9px;
									@include mixins.ratio_images('../../images/ico_captcha_refresh_mok', 'png', 13px, 13px);
									vertical-align: top;
								}

								.ico_speak {
									display: inline-block;
									width: 14px;
									height: 13px;
									margin-top: 10px;
									@include mixins.ratio_images('../../images/ico_captcha_speak_mok', 'png', 14px, 13px);
									vertical-align: top;
								}

								+ .btn_captcha {
									margin: 4px 0 0;
								}
							}
						}
					}

					.form_ip {
						margin-top: 10px;
					}
				}

				// 회원 상단 아이콘 + 텍스트 안내 영역
				.member_guide_wrap {
					display: block;
					margin-bottom: 30px;

					.member_guide_title {
						.ico_img_box {
							display: block;
							margin-bottom: 20px;
						}
						.guide_title {
							display: block;
							color: variables.$textColorBlack;
							@include mixins.font_size_md;
							font-weight: variables.$fontWeightMedium;

							.warning {
								color: variables.$textColorRed;
							}
						}
					}
					.member_guide_cont {
						margin-top: 12px;

						.info_text {
							& + .form_chk,
							& + .form_rdo {
								margin-top: 20px;
							}

							& + .bul_item_asterisk {
								margin-top: 10px;
							}
						}
					}
				}

				// 아이디 찾기 결과 리스트
				.id_result_list_wrap {
					display: block;

					// id item
					.id_result_list_item {
						@include mixins.justify_width(center);
						padding: 20px 16px;
						border: 1px solid variables.$colorGray04;
						border-radius: 10px;
						box-sizing: border-box;

						.left_area {
							@include mixins.justify_width(center);
							flex: 1;

							.id_rdo_wrap {
								flex: 0 auto;
								margin-right: 12px;

								.form_rdo {
									margin-bottom: 4px;
								}
							}

							.id_text_wrap {
								flex: 1;

								.id_name_box {
									word-break: break-all;
									font-size: 0;

									.id_label {
										display: inline-block;
										margin-right: 8px;
										color: variables.$textColorGray;
										@include mixins.font_size_sm;
										font-weight: variables.$fontWeightRegular;
									}
									.id_name {
										display: inline-block;
										color: variables.$textColorBlack;
										@include mixins.font_size_sm;
										font-weight: variables.$fontWeightRegular;
									}
								}
								.id_info_box {
									display: block;
									margin-top: 6px;

									.id_info_inner {
										display: block;
										font-size: 0;

										.id_info {
											display: inline-block;
											color: variables.$textColorLightGray;
											@include mixins.font_size_xs;
											font-weight: variables.$fontWeightRegular;

											.date {
												font-weight: variables.$fontWeightMedium;
											}
										}
										.gap {
											display: inline-block;
											width: 1px;
											height: 9px;
											margin: 4px 8px 0;
											background-color: #ccc;
											font-size: 0;
											vertical-align: top;
										}

										& ~ .id_info_inner {
											margin-top: 10px;
										}
									}
								}
							}
						}
						.right_area {
							flex: 0 auto;
							margin-left: 10px;
						}

						& ~ .id_result_list_item {
							margin-top: 10px;
						}

						&.disabled {
							.left_area {
								.id_text_wrap {
									.id_name_box {
										.id_name {
											color: variables.$textColorLightGray;
										}
									}
								}
							}
						}

						&.no_rdo {
							.left_area {
								.id_text_wrap {
									.id_name_box {
										.id_name {
											font-weight: variables.$fontWeightBold;
										}
									}
								}
							}
						}

						@include mixins.respond-to(mobileXs){
							@include mixins.justify_width(start, start, column);
							align-items: stretch;

							.right_area {
								margin-top: 20px;
								margin-left: 34px;
							}
						}
					}

					.id_result_list {
						& + .id_result_list {
							margin-top: 32px;
						}
					}

					& + .info_text_box {
						margin-top: 10px;
					}
				}

				// tab 영역
				> .tab_wrap {
					padding: 0 20px;
					margin-top: 10px;

					.tab_list_wrap {
						.tabs {
							padding: 0;
						}
					}
				}

				// 페이지 하단 버튼 영역
				.btn_wrap {
					&.page_bottom {
						margin-top: 40px;
					}
				}

				.fold_box_wrap {
					.fold_box_list {
						.fold_box {
							.fold_box_contents {
								.cert_type_box {
									.btn_cert_type {
										height: 115px;
									}
								}
							}
							.cert_guide_box {
								margin-top: 10px;
								.info_text {
									& + .bul_item_asterisk {
										margin-top: 6px;
									}
								}
							}
						}

					}
				}
			}
		}

		.footer_wrapper {
			margin-top: auto;
			height: 60px;

			.footer_body {
				padding: 20px 20px;
				border-top: none;
				text-align: center;

				.copyright {
					display: inline-block;
					@include mixins.font_size_xxs;
					color: variables.$textColorLightGray;
				}
			}
		}

		.docker_wrapper{
			.btn_go_top{
				bottom: 18px;
			}
		}
	}
}